
.index-page{background:url("../assets/wavy.png") #3cbaff no-repeat content-box;background-position:left bottom 98px;background-size:100% 215px
}
.index-page-content{width:1200px;margin:0 auto;overflow:hidden;color:#ffffff;padding:174px 0 40px 0
}
.index-page-content .photo-images{width:468px;float:left
}
.index-page-content .enter-container{margin-left:668px
}
.index-page-content .enter-container h1{width:350px;border-bottom:1px solid #ffffff;font-size:48px
}
.index-page-content .enter-container .slogan,.index-page-content .enter-container .translation{margin-top:20px;font-weight:100;color:#FFFFFF
}
.index-page-content .enter-container .slogan{font-size:26px;letter-spacing:27px
}
.index-page-content .enter-container .translation{font-size:18px;letter-spacing:6px
}
.index-page-content .enter-content{margin-top:50px
}
.index-page-content .enter-content .enter-item{vertical-align:middle;width:200px;height:220px;display:inline-block;text-align:center;background-color:#ffffff;border-radius:10px;margin-right:50px;cursor:pointer
}
.index-page-content .enter-content .enter-item a{text-decoration:none
}
.index-page-content .enter-content .enter-item:last-child{margin-right:0
}
.index-page-content .enter-content .enter-item h3{color:#3cbaff;font-size:16px
}
.index-page-content .enter-content .enter-item .enter-item-component{margin-top:35px;width:72px
}
.index-page-content .enter-content .enter-item .enter-item-resources{margin-top:47px;margin-bottom:10px;width:90px
}
.index-page-content .enter-content .enter-item span{margin-top:8px;font-size:14px;color:#7A797B
}
footer.copyright{font-size:12px;text-align:center;height:98px;line-height:98px;background-color:#ffffff;color:#98A0AA
}
footer.copyright .ui-name{font-size:16px;margin-right:18px
}


