html,body{
    width:100%;
    height:100%;
}
body{
    color: #5e5e5e;
    font-family: -apple-system,Microsoft YaHei,Arial,Microsoft JhengHei,sans-serif;
}
a{
    color: #5e5e5e;
}
.wrap {
    width:100%;
    min-height: 100%;
    overflow-x: auto;
}
.page {

}
.button{
    &:hover:before{
        content: '';
        width:100%;
        height: 100%;
        background-color: #000;
        opacity: 0.2;
    }
}
/*=====header=====*/
.header {
    //width: 1173px;
    height: 80px;
    margin: 28px;
    img {
        height: 100%;
    }
}
/*=====header=====*/
/*====main-menu===*/
.main-menu {
    width: 100%;
    height: 98px;
    background: #3cbaff;
    position: relative;

    h2 {
        font-size: 28px;
        color: #fff;
        line-height: 98px;
        margin-left: 50px;
        cursor: pointer;
    }

    ul {
        position: absolute;
        top: 0;
        right: 50px;

        li {
            display: inline-block;
            height: 98px;
            line-height: 98px;
            color: #fff;
            opacity: 0.5;
            margin-left: 40px;
            font-size: 14px;
            cursor: pointer;
            &.active {
                opacity: 1;
            }
            a{
                color:#ffffff;
                text-decoration: none;
            }
        }
    }
    
}
/*====main-menu===*/
/*====doc page====*/
.page-doc {
    display: flex;
    display: -webkit-box;
    .left-side-bar {
        min-width: 240px;
    }

    .main-doc-area {
        flex: 1;
        -webkit-box-flex: 1;
        padding:20px 50px 50px;
    }

    .demo-area {
        width:400px;
    }
}
/*====doc page====*/


